import { useFormContext } from 'react-hook-form';
import { FormField, FormItem, FormLabel, FormControl, FormMessage } from '@/components/ui/form';
import { Switch } from '@/components/ui/switch';

interface Props {
    className?: string;
    name: string;
    label?: string;
}

export default function SwitchInputForm({ className, name, label }: Props) {
    const { control } = useFormContext();

    return (
        <FormField
            control={control}
            name={name}
            render={({ field }) => (
                <FormItem className={className}>
                    <FormLabel className="!mt-0 ms-2 items-center tracking-normal text-start text-[#080614] text-base font-bold leading-[100%]">
                        {label}
                    </FormLabel>
                    <FormControl>
                        <Switch
                            dir="ltr"
                            className="data-[state=checked]:bg-primery-blue-600 !mt-0"
                            checked={field.value}
                            onCheckedChange={field.onChange}
                        />
                    </FormControl>
                    <FormMessage />
                </FormItem>
            )}
        />
    );
}
