import { Form } from '@/components/ui/form';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { apiGet, apiPost } from '@/lib/axios/axios-utils';
import { endpoints } from '@/lib/axios/axios-global';
import { useEffect, useMemo, useState } from 'react';
import TimeInputForm from '@/components/forms/TimeInputForm';
import { SecondaryButton } from '@/components/buttons/SecondaryButton';
import { PrimaryButton } from '@/components/buttons/PrimaryButton';
import SearchableSelectInputForm from '@/components/forms/SearchableSelectInputForm';
import SelectInputForm from '@/components/forms/SelectInputForm';
import { toast } from 'sonner';
import MainHeading from '@/components/heading/MainHeading';
import DaysSwitchInputForm from '@/components/forms/DaysSwitchInputForm';
import TextInputForm from '@/components/forms/TextInputForm';
import { AddShiftFormValues, AddShiftSchema } from '../schemas/addShift/add-attendance-schema';
import { useNavigate } from 'react-router-dom';
import { shiftInfo } from '../../types/shift.types';
import { ShiftType } from '@/types/shift.types';

interface Props {
    shift?: shiftInfo | null;
}

export default function AddShiftForm({ shift }: Props) {
    const [error, setError] = useState<string | null>(null);
    const [loading, setLoading] = useState(false);
    const [shifts, setShifts] = useState<{ name: string; value: string }[]>([]);
    const [shiftsError, setShiftsError] = useState<string | null>(null);
    const navigate = useNavigate();

    const defaultValues = useMemo(
        () => ({
            name: shift?.name || '',
            type: shift?.type || '',
            sunday: shift?.sunday || false,
            monday: shift?.monday || false,
            tuesday: shift?.tuesday || false,
            wednesday: shift?.wednesday || false,
            thursday: shift?.thursday || false,
            friday: shift?.friday || false,
            saturday: shift?.saturday || false,
            shift_start_time: shift?.shift_start_time || '',
            shift_end_time: shift?.shift_end_time || '',
            early_entry_time: shift?.early_entry_time || '',
            early_exit_time: shift?.early_exit_time || '',
            late_entry_time: shift?.late_entry_time || '',
            late_exit_time: shift?.late_exit_time || '',
            delay_minutes: shift?.delay_minutes || '',
            delay_calculation_method: shift?.delay_calculation_method || '',
        }),
        [shift],
    );

    const form = useForm<AddShiftFormValues>({
        resolver: zodResolver(AddShiftSchema),
        defaultValues,
    });

    const onSubmit = async (data: AddShiftFormValues) => {
        setLoading(true);
        setError(null); // Clear previous errors
        const formatedData = {
            ...data,
            sunday: data.sunday ? 1 : 0,
            monday: data.monday ? 1 : 0,
            tuesday: data.tuesday ? 1 : 0,
            wednesday: data.wednesday ? 1 : 0,
            thursday: data.thursday ? 1 : 0,
            friday: data.friday ? 1 : 0,
            saturday: data.saturday ? 1 : 0,
        };
        try {
            await apiPost({
                endpoint: shift ? endpoints.shifts.update + shift.id : endpoints.shifts.create,
                body: formatedData,
                config: { headers: { 'Content-Type': 'multipart/form-data' } },
            });

            navigate('/human-resources/shifts');
            toast(`${shift ? 'تم تعديل الوردية بنجاح' : 'تم اضافة الوردية بنجاح'}`);
        } catch (error: any) {
            if (error.response?.data?.errors) {
                // Handle field-specific errors
                Object.entries(error.response.data.errors).forEach(([field, messages]) => {
                    form.setError(field as any, {
                        type: 'manual',
                        message: (messages as string[]).join(', '),
                    });
                });
            } else {
                // Handle generic error
                let errorMessage = 'حدث خطأ أثناء إرسال البيانات. يرجى المحاولة مرة أخرى.';
                if (error.response) {
                    errorMessage = error.response.data.msg || errorMessage;
                } else if (error.request) {
                    errorMessage = 'تعذر الاتصال بالخادم. يرجى التحقق من اتصالك بالإنترنت.';
                }
                setError(errorMessage);
            }
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => {
        const fetchShifts = async () => {
            try {
                const response = await apiGet({ endpoint: endpoints.shifts.list });

                const options = response.data.data.map((shift: ShiftType) => {
                    return { name: shift.name, value: String(shift.name) };
                });
                setShifts(options);
                setShiftsError(null);
            } catch (error) {
                setShiftsError('Error fetching shifts. Please try again later.');
            }
        };

        fetchShifts();
    }, []);

    return (
        <>
            <Form {...form}>
                <form onSubmit={form.handleSubmit(onSubmit)} className="mt-6 mb-24">
                    {error && (
                        <div className="mb-4 p-4 bg-red-100 border border-red-400 text-red-700 rounded-lg">
                            <div className="flex justify-between items-center">
                                <span className="">{error}</span>
                                <button onClick={() => setError(null)} className="text-red-700 hover:text-red-900">
                                    ×
                                </button>
                            </div>
                        </div>
                    )}
                    <MainHeading text="تفاصيل الوردية" />
                    <div className="grid grid-cols-2 gap-x-12">
                        <SearchableSelectInputForm
                            name="name"
                            placeholder="بحث عن اسم أو كود العميل"
                            label="اسم العميل"
                            options={shifts}
                            error={shiftsError}
                            data={true}
                        />
                        <SelectInputForm
                            name="type"
                            options={[
                                { name: 'صباحى', value: 'am' },
                                { name: 'مسائى', value: 'pm' },
                            ]}
                            label="النوع"
                            placeholder="صباحى - مسائى"
                        />
                    </div>
                    <MainHeading text="أيام العمل" className="mt-6" />
                    <div className="mt-8 mb-8 grid grid-cols-2 gap-6">
                        <DaysSwitchInputForm
                            className="w-full border border-[#E8E7EA] rounded-xl px-4 py-[26px] flex justify-between items-center"
                            name="sunday"
                            label="الأحد"
                        />
                        <DaysSwitchInputForm
                            className="w-full border border-[#E8E7EA] rounded-xl px-4 py-[26px] flex justify-between items-center"
                            name="monday"
                            label="الإثنين"
                        />
                        <DaysSwitchInputForm
                            className="w-full border border-[#E8E7EA] rounded-xl px-4 py-[26px] flex justify-between items-center"
                            name="tuesday"
                            label="الثلاثاء"
                        />
                        <DaysSwitchInputForm
                            className="w-full border border-[#E8E7EA] rounded-xl px-4 py-[26px] flex justify-between items-center"
                            name="wednesday"
                            label="الأربعاء"
                        />
                        <DaysSwitchInputForm
                            className="w-full border border-[#E8E7EA] rounded-xl px-4 py-[26px] flex justify-between items-center"
                            name="thursday"
                            label="الخميس"
                        />
                        <DaysSwitchInputForm
                            className="w-full border border-[#E8E7EA] rounded-xl px-4 py-[26px] flex justify-between items-center"
                            name="friday"
                            label="الجمعة"
                        />
                        <DaysSwitchInputForm
                            className="w-full border border-[#E8E7EA] rounded-xl px-4 py-[26px] flex justify-between items-center"
                            name="saturday"
                            label="السبت"
                        />
                    </div>
                    <MainHeading text="قواعد الحضور" className="mt-6" />
                    <div className="grid grid-cols-2 gap-x-6 gap-y-4">
                        <TimeInputForm
                            name="shift_start_time"
                            label="بداية الوردية"
                            placeholder="09:00"
                            className="flex flex-col"
                        />
                        <TimeInputForm
                            name="shift_end_time"
                            label="نهاية الوردية"
                            placeholder="17:00"
                            className="flex flex-col"
                        />
                        <TimeInputForm
                            name="early_entry_time"
                            label="بداية تسجيل الدخول"
                            placeholder="07:00"
                            className="flex flex-col"
                        />
                        <TimeInputForm
                            name="early_exit_time"
                            label="بداية تسجيل الخروج"
                            placeholder="15:00"
                            className="flex flex-col"
                        />
                        <TimeInputForm
                            name="late_entry_time"
                            label="فترة سماح التأخير:"
                            placeholder="15 د"
                            className="flex flex-col"
                        />
                        <TimeInputForm
                            name="late_exit_time"
                            label="فترة سماح التأخير (الخروج):"
                            placeholder="15 د"
                            className="flex flex-col"
                        />
                        <TextInputForm
                            name="delay_minutes"
                            type="number"
                            label="التأخير"
                            placeholder=""
                            className="flex flex-col"
                        />
                        <TextInputForm
                            name="delay_calculation_method"
                            type="number"
                            label="حساب التأخير"
                            placeholder="بعد بداية الوردية + مهلة التأخير"
                            className="flex flex-col"
                        />
                    </div>

                    <div className="flex flex-row justify-end gap-4 mt-6">
                        <SecondaryButton
                            type="button"
                            className="text-base leading-5 tracking-normal !py-4 !px-16 bg-red-error-50 text-red-error-600 hover:text-white-blue-50 hover:bg-red-error-600  font-medium"
                        >
                            إلغاء
                        </SecondaryButton>
                        <PrimaryButton
                            type="submit"
                            className="text-base leading-5 tracking-normal font-medium !py-4 !px-32"
                        >
                            {loading ? 'جاري الحفظ...' : 'حفظ'}
                        </PrimaryButton>
                    </div>
                </form>
            </Form>
        </>
    );
}
