import { z } from 'zod';

export const AddShiftSchema = z.object({
    name: z.string().min(1, 'Name field is required.'),
    type: z.string().min(1, 'Type name field is required.'),
    sunday: z.boolean(),
    monday: z.boolean(),
    tuesday: z.boolean(),
    wednesday: z.boolean(),
    thursday: z.boolean(),
    friday: z.boolean(),
    saturday: z.boolean(),
    shift_start_time: z.string().min(1, 'Shift start is required'),
    shift_end_time: z.string().min(1, 'Shift end is required'),
    late_entry_time: z.string().min(1, 'Late entry is required'),
    early_entry_time: z.string().min(1, 'Early entry is required'),
    late_exit_time: z.string().min(1, 'late Exit is required'),
    early_exit_time: z.string().min(1, 'Early exit is required'),
    delay_minutes: z.string().min(1, 'Delay minutes is required'),
    delay_calculation_method: z.string().min(1, 'Delay calculation method is required'),
});

export type AddShiftFormValues = z.infer<typeof AddShiftSchema>;
